import React, { useState, useEffect, Fragment } from 'react';
import io from "socket.io-client";
import Mensajes from './componentes/Mensajes/Mensajes.jsx';
import Input from './componentes/input/Input.jsx';
import './App.css';
import Entrar from './componentes/entrar/Entrar.jsx';
import ScrollToBottom from 'react-scroll-to-bottom';

let socket;
function App() {

  const [mensajes, setMensajes] = useState([]);
  const [mensaje, setMensaje] = useState('');
  const [nombre, setNombre] = useState('');
  const URL = 'https://chat-socket-by-ezequieldev.herokuapp.com/';
  
  useEffect(() => {
    socket = io(URL);
    socket.on("mensaje", msg => {
      console.log(msg)
      setMensajes(mensajes => [ ...mensajes, msg ]);
      });
 
  },[]);
  const enviarMensaje = (e) =>{
    e.preventDefault();

    if(mensaje) {
      socket.emit('enviarMensaje', mensaje);
    }
    setMensaje('')
  }
 const enviarNombre = (e) => {
   e.preventDefault()

    setNombre(nombre)
    setNombre('')
    console.log(nombre)
  }

  return (
    <Fragment>
        <div className="container all">       
            <div className="chat">
              <Mensajes className="hijos " mensajes={mensajes} />
              <Input className="hijos" mensaje={mensaje} setMensaje={setMensaje} enviarMensaje={enviarMensaje} /> 
            </div>
      </div>
    </Fragment>
  );
}

export default App;
