import React from 'react';

const Input = ({mensaje, setMensaje, enviarMensaje}) => {

    return ( 
        <div className="container mt-2">
            <div className="mx-auto form-row">
                <input type="text" className="form-control col-sm" placeholder="Escribe un mensaje"
                placeholder='Escribe un Algo....'
                name="mensaje"
                value={mensaje}
                onChange={({ target: { value } }) => setMensaje(value)}
                onKeyPress={event => event.key === 'Enter' ? enviarMensaje(event) : null}
                />
                <input type="button" 
                className="btn btn-primary mx-2 " 
                value="Enviar"
                onClick={e => enviarMensaje(e)}
                
                />
            </div>
        </div>
     );
}
 
export default Input;