import React from 'react';

const Mensajes = ({mensajes}) => {
    return ( 
        <ul className="list-group">
            {
               mensajes.map( (mensaje, index) => 
               <li key={index} className="list-group-item">
                  <strong>Mensaje:</strong> {mensaje}
               </li>)
              
            }
            
        </ul>
     );
}
 
export default Mensajes;